	module.exports = "<h2 id=overview>Overview</h2> <p>A Grid layout framework including responsive columns, row spans and gutters. Breakpoints: base (no breakpoint value), -on-sm, -on-md, -on-lg, -on-xl. <strong>Example .pf-m-6-col-on-lg</strong></p> <h2 id=usage>Usage</h2> <table> <thead> <tr> <th>Class</th> <th>Applied To</th> <th>Outcome</th> </tr> </thead> <tbody> <tr> <td><code>.pf-l-grid</code></td> <td><code>&lt;div&gt;</code></td> <td>Initializes the grid layout.</td> </tr> <tr> <td><code>.pf-l-grid__item</code></td> <td><code>&lt;div&gt;</code></td> <td>Explicitly sets a child of the grid. This class isn&#39;t necessary, but it is included to keep inline with BEM convention, and to provide an entity that will later be used for applying modifiers.</td> </tr> <tr> <td><code>.pf-m-gutter</code></td> <td><code>.pf-l-grid</code></td> <td>Adds grid gutters.</td> </tr> <tr> <td><code>.pf-m-all-{1-12}-col{-on-[breakpoint]}</code></td> <td><code>.pf-l-grid</code></td> <td>Defines grid item size on grid container.</td> </tr> <tr> <td><code>.pf-m-{1-12}-col{-on-[breakpoint]}</code></td> <td><code>.pf-l-grid__item</code></td> <td>Defines grid item size. Although not required, they are strongly suggested. If not used, grid item will default to 12 col.</td> </tr> <tr> <td><code>.pf-m-{2-x}-row{-on-[breakpoint]}</code></td> <td><code>.pf-l-grid__item</code></td> <td>Defines grid item row span. For row spans to function correctly, the value of of the current row plus the grid items to span must be equal to or less than 12. Example: .pf-m-8-col.pf-m-2-row + .pf-m-4-col + .pf-m-4-col. There is no limit to number of spanned rows.</td> </tr> </tbody> </table> ";

